<template>
  <div class="video" v-play="play">
    <div class="video-box" v-for="(item, index) in videos" :key="index" v-show="item.show">
      <span id="play" v-show="item.isPlay" @click.stop="play">
        <img src="https://overseas-img.qq.com/images/chiji/cp/a20181207xdbb/f.png" title="点击播放">
      </span>
      <span id="btn_jump" v-show="item.btn_jump" @click.stop="stopPlay">点击跳过 > ></span>
      <img
        v-show="item.slogan"
        src="https://overseas-img.qq.com/images/chiji/cp/a20181207xdbb/slogan.png"
        id="slogan"
      >
      <a href="https://vikendicn.playbattlegrounds.com" id="img-toweb" v-show="item.slogan">
        <img src="https://overseas-img.qq.com/images/chiji/cp/a20181207xdbb/toweb.png">
      </a>
      <video
        :src="item.src"
        :autoplay="item.autoplay"
        :loop="item.loop"
        :id="item.id"
        @ended="ended(item)"
      ></video>
    </div>
  </div>
</template>

<script>
import datas from "@/data/index.js";
export default {
  data() {
    return {
      videos: datas
    };
  },
  methods: {
    play() {
      playVideo("video-1", "video-2");
    },
    ended(item) {
      if (item.id === "video-2") {
        playVideo(item.id, "video-3");
      }
    },
    stopPlay() {
      playVideo("video-2", "video-3");
    }
  },
  directives: {
    play: {
      inserted(el, binding) {
        document.addEventListener("keyup", event => {
          if (event.keyCode === 70) {
            if (typeof binding.value === "function") binding.value();
          }
        });
      }
    }
  }
};

function playVideo(id_1, id_2) {
  document.getElementById(id_1).pause();
  document.getElementById(id_1).parentNode.style.display = "none";
  document.getElementById(id_2).play();
  document.getElementById(id_2).parentNode.style.display = "block";
}
</script>

<style scoped lang="less">
.video {
  width: 100%;
  height: 100%;
  min-width: 1000px;
  overflow: hidden;
  position: relative;
  > .video-box {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 9;
    font-size: 0;
    > #play {
      font-size: 0;
      display: block;
      width: 12%;
      position: absolute;
      top: 50%;
      right: 24%;
      z-index: 10;
      cursor: pointer;
      animation: scale 1.5s infinite linear alternate;
      > img {
        display: block;
        width: 100%;
        height: auto;
      }
    }
    > #btn_jump {
      display: block;
      position: absolute;
      top: 2%;
      right: 2%;
      z-index: 10;
      color: #fff;
      cursor: pointer;
      font-size: 16px;
    }
    > #slogan {
      display: block;
      width: 32%;
      height: auto;
      top: 10%;
      left: 50%;
      margin-left: -15.5%;
      position: absolute;
      z-index: 10;
    }
    > #img-toweb {
      display: block;
      font-size: 0;
      position: absolute;
      width: 14%;
      height: auto;
      bottom: 50px;
      left: 50%;
      z-index: 10;
      margin-left: -7%;
      cursor: pointer;
      > img {
        display: block;
        width: 100%;
        height: auto;
      }
    }
    > video {
      min-height: 100%;
      min-width: 100%;
      transform: translate(-50%, -50%);
      position: absolute;
      top: 50%;
      left: 50%;
    }
  }
}

@keyframes scale {
  from {
    transform: scale(1);
  }

  to {
    transform: scale(1.05);
    filter: brightness(1.2);
  }
}
</style>


